$font: 'Grenze Gotisch', cursive;

@import url('https://fonts.googleapis.com/css2?family=Grenze+Gotisch:wght@100;200;300;500&display=swap');

$backgroundColor: #fcfcfc;
$brownColor: #4a332d;
$greenColor: #999b84;
$yellowColor: rgba(249, 242, 229, 0.5);

@mixin flex($display, $direction, $justify, $align) {
  display: $display;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}
